//Валидация

const defaulValidationErrorMessage = "Возникла ошибка при заполнении формы. Пожалуйста, проверьте введённые данные.";

const validationRegex: Record<string, RegExp> = {
	//last_name: NAMEREGEX,
	//name: NAMEREGEX,
	email: new RegExp(/^([A-Za-z0-9_\.-]+)@([A-Za-z0-9_\.-]+)\.([a-z\.]{2,6})$/),
	//phone: PHONEREGEX,
	//password: PASSWORDREGEX,
	//login: LOGINREGEX
}

export const validValue = function(input: HTMLInputElement): boolean {
	const validationType = input.getAttribute("data-validation-type");
	const errorText = input.getAttribute("data-error-text");

	const value = input.value;
	const regex = validationRegex[validationType];

	const inputWrapper = input.parentElement;
	const errorBlock = inputWrapper.querySelector(".error-text-block");

	if (regex && !regex.test(value)) {
		inputWrapper.classList.add('error-input');
		console.log(errorBlock)
		console.log(errorText)
		if (errorBlock){
			if (errorText){
				errorBlock.textContent = errorText;
			} else {
				errorBlock.textContent = defaulValidationErrorMessage;
			}
		}		
		return false
	}
	inputWrapper.classList.remove('error-input');
	if (errorBlock){
		errorBlock.textContent = "";
	}
	return true
}